<template>
  <el-button @click="goBack"></el-button>
  <el-button @click="getOciCfgDetails" :loading="flushLoading"></el-button>
  <span style="margin-left: 20px;"><el-text type="primary">{{ this.user.username }}  </el-text></span>
  <el-divider class="divider"/>
  <div class="demo-collapse">
    <el-collapse accordion>
      <el-collapse-item name="1">
        <template #title>
          <el-icon class="header-icon">
            <info-filled/>
          </el-icon>
          API
        </template>
        <div><span style="margin-left: 20px;"><el-text type="info">region={{
            this.ociCfgDetails.region
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">user={{ this.ociCfgDetails.userId }}</el-text></span>
        </div>
        <div><span style="margin-left: 20px;"><el-text type="info">tenancy={{
            this.ociCfgDetails.tenantId
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">fingerprint={{
            this.ociCfgDetails.fingerprint
          }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">key_file={{
            this.ociCfgDetails.privateKeyPath
          }}</el-text></span></div>
      </el-collapse-item>
    </el-collapse>
  </div>
  <div class="instance-list">
    <div v-for="instance in ociCfgDetails.instanceList" :key="instance.ocId" class="instance-card">
      <div class="header">
        <el-icon class="server-icon">
          <cpu/>
        </el-icon>
        <h3>{{ instance.name }}</h3>
      </div>
      <el-divider class="divider"/>
      <p><strong>Region</strong>
        <el-tag style="margin-left: 5%">{{ instance.region }}</el-tag>
      </p>
      <p><strong></strong>
        <el-tag :type="instance.state.indexOf('RUNNING') !== -1 ? 'success' : 'info'" style="margin-left: 5%">
          {{ instance.state }}
        </el-tag>
      </p>
      <p><strong>Shape</strong>
        <el-tag :type="instance.shape.indexOf('A1') !== -1 ? 'success' : 'info'" style="margin-left: 5%">
          {{ instance.shape }}
        </el-tag>
      </p>
      <div class="ip-list" style="display: flex; align-items: center;">
        <p style="margin: 0; margin-right: 10px; font-weight: bold;">IP</p>
        <div v-for="(ip, ipIndex) in instance.publicIp" :key="ipIndex" class="ip-item" style="margin-right: 10px;">
          <el-tag effect="plain"> {{ ip }}</el-tag>
        </div>
      </div>
      <p><strong></strong>
        <el-tag style="margin-left: 5%" effect="plain" type="info">
          {{ instance.ocpus }}  / {{ instance.memory }} GB / {{ instance.bootVolumeSize }} GB
        </el-tag>
      </p>
      <p><strong></strong>
        <el-tag style="margin-left: 5%" effect="plain" type="info">
          {{ instance.createTime }}
        </el-tag>
      </p>
      <el-divider class="divider"/>
      <el-button-group class="ml-4">
        <el-tooltip content="IP" placement="bottom">
          <el-button @click="handleChangeIp(instance.ocId)" icon="HelpFilled"
                     v-if="instance.enableChangeIp !== 1"
                     type="primary">
          </el-button>
        </el-tooltip>

        <el-tooltip content="IP" placement="bottom">
          <el-button @click="handleStopChangeIp(instance.ocId)" icon="HelpFilled"
                     v-if="instance.enableChangeIp === 1"
                     type="danger">
          </el-button>
        </el-tooltip>

        <el-tooltip content="" placement="bottom">
          <el-button type="success" icon="VideoPlay"
                     :disabled="instance.state.indexOf('RUNNING') !== -1"
                     v-if="instance.state.indexOf('RUNNING') === -1"
                     @click="updateInstanceState(instance.ocId, 'START')"/>
        </el-tooltip>

        <el-tooltip content="" placement="bottom">
          <el-button type="info" icon="VideoPause"
                     :disabled="instance.state.indexOf('STOPPED') !== -1"
                     v-if="instance.state.indexOf('STOPPED') === -1"
                     @click="updateInstanceState(instance.ocId, 'STOP')"/>
        </el-tooltip>

        <el-tooltip content="" placement="bottom">
          <el-button type="warning" icon="Refresh"
                     @click="updateInstanceState(instance.ocId, 'RESET')"/>
        </el-tooltip>

        <el-tooltip content="" placement="bottom">
          <el-button type="danger" icon="Delete" :disabled="instance.state.indexOf('TERMINATING') !== -1"
                     @click="handleTerminateInstance(instance.ocId)"/>
        </el-tooltip>
      </el-button-group>
    </div>
  </div>
  <el-dialog v-model="dialogVisible" title=" CIDR IP" width="40%" @close="dialogVisible = false">
    <el-tag v-for="tag in changeIpParams.cidrList" :key="tag" closable @close="handleClose(tag)"> {{ tag }}</el-tag>
    <el-input v-if="inputVisible" ref="InputRef" v-model="inputValue" class="w-20" @keyup.enter="handleInputConfirm"
              @blur="handleInputConfirm"/>
    <el-button v-else class="button-new-tag" size="small" @click="showInput"> + New CIDR</el-button>
    <template #footer>
      <el-button @click="dialogVisible = false"></el-button>
      <el-button type="primary" @click="changeIp" :loading="changeIpLoading">IP</el-button>
    </template>
  </el-dialog>
  <!--  -->
  <el-dialog
      title=""
      v-model="terminateDialogVisible"
      width="500px"
      @close="terminateDialogVisible = false"
  >
    <div>
      <p></p>
      <el-row>
        <!--  -->
        <el-col :span="24">
          <el-checkbox v-model="retainBootVolume">
            
          </el-checkbox>
        </el-col>
      </el-row>
      <el-divider class="divider"/>
      <el-row>
        <!--  -->
        <el-col :span="24">
          <el-form :model="form" ref="form" label-width="70px">
            <el-form-item label="">
              <el-input
                  v-model="form.verificationCode"
                  placeholder=""
                  maxlength="10"
                  clearable
              ></el-input>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <template #footer>
      <el-button @click="terminateDialogVisible = false"></el-button>
      <el-button @click="sendCaptcha" :loading="sendLoading"></el-button>
      <el-button
          type="danger"
          :disabled="form.verificationCode === ''"
          :loading="terminateLoading"
          @click="terminateInstance"
      >
        
      </el-button>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage, ElMessageBox} from "element-plus";
import {cpu} from '@element-plus/icons-vue';

export default {
  components: {cpu},
  data() {
    return {
      flushLoading: false,
      changeIpLoading: false,
      sendLoading: false,
      dialogVisible: false,
      terminateDialogVisible: false,
      terminateLoading: false, // 
      retainBootVolume: false, // 
      form: {
        verificationCode: "", // 
      },
      inputVisible: false,
      inputValue: '',
      user: {},
      changeIpParams: {
        ociCfgId: '',
        instanceId: '',
        cidrList: []
      },
      ociCfgDetails: {},
      UpdateInstanceStateParams: {
        ociCfgId: '',
        instanceId: '',
        action: ''
      },
      SendCaptchaParams: {
        ociCfgId: '',
        instanceId: ''
      },
      TerminateInstanceParams: {
        ociCfgId: '',
        instanceId: '',
        preserveBootVolume: 0,
        captcha: ''
      },
    };
  },
  mounted() {
    this.user = JSON.parse(this.$route.query.row || '{}')
    this.changeIpParams.ociCfgId = this.user.id
    this.getOciCfgDetails()
  },
  methods: {
    goBack() {
      this.$router.back(); // 
    },
    handleClose(tag) {
      const index = this.changeIpParams.cidrList.indexOf(tag);
      if (index !== -1) {
        this.changeIpParams.cidrList.splice(index, 1);
      }
    },
    handleStopChangeIp(ocId) {
      const confirmed = window.confirm('IP?');
      if (confirmed) {
        this.stopChangeIp(ocId);
      } else {
        this.$message.info('');
      }
    },
    stopChangeIp(ocId) {
      this.$axios.post('/oci/stopChangeIp', {instanceId: ocId})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
            this.getOciCfgDetails()
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.InputRef.focus();
      });
    },
    handleInputConfirm() {
      if (this.inputValue && !this.changeIpParams.cidrList.includes(this.inputValue)) {
        this.changeIpParams.cidrList.push(this.inputValue);
      }
      this.inputValue = '';
      this.inputVisible = false;
    },
    async getOciCfgDetails() {
      this.ociCfgDetails.instanceList = []
      this.flushLoading = true
      await this.$axios.post('/oci/details', {id: this.changeIpParams.ociCfgId})
          .then(response => {
            if (response.data.success) {
              this.ociCfgDetails = response.data.data
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
              this.flushLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleChangeIp(id) {
      this.dialogVisible = true
      this.changeIpParams.instanceId = id
    },
    changeIp() {
      this.changeIpLoading = true
      this.$axios.post('/oci/changeIp', {...this.changeIpParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateInstanceState(id, action) {
      //  action 
      let actionMessage = '';
      switch (action) {
        case 'START':
          actionMessage = '';
          break;
        case 'STOP':
          actionMessage = '';
          break;
        case 'RESET':
          actionMessage = '';
          break;
        default:
          actionMessage = '';
      }
      try {
        // 
        const confirmResult = await ElMessageBox.confirm(
            actionMessage, // 
            '', // 
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning', // 
            }
        );

        //  ""
        if (confirmResult === 'confirm') {
          this.UpdateInstanceStateParams.ociCfgId = this.user.id
          this.UpdateInstanceStateParams.instanceId = id
          this.UpdateInstanceStateParams.action = action

          await this.$axios
              .post('/oci/updateInstanceState', {...this.UpdateInstanceStateParams})
              .then((response) => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000, // 
                  });
                  this.getOciCfgDetails();
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000,
                  });
                }
              })
              .catch((error) => {
                console.error('Error:', error);
                ElMessage({
                  message: '',
                  type: 'error',
                  duration: 2000,
                });
              });
        }
      } catch (cancelError) {
        //  "" 
        ElMessage({
          message: '',
          type: 'info',
          duration: 2000,
        });
      }
    },
    handleTerminateInstance(id) {
      this.TerminateInstanceParams.instanceId = id
      this.terminateDialogVisible = true
    },
    sendCaptcha() {
      this.sendLoading = true
      this.SendCaptchaParams.ociCfgId = this.user.id
      this.SendCaptchaParams.instanceId = this.TerminateInstanceParams.instanceId
      this.$axios.post('/oci/sendCaptcha', {...this.SendCaptchaParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.sendLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async terminateInstance() {
      this.terminateLoading = true;

      this.TerminateInstanceParams.ociCfgId = this.user.id
      this.TerminateInstanceParams.preserveBootVolume = this.retainBootVolume === true ? 1 : 0
      this.TerminateInstanceParams.captcha = this.form.verificationCode
      // 
      this.$axios.post('/oci/terminateInstance', {...this.TerminateInstanceParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 
              });
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
            ElMessage({
              message: "",
              type: "error",
              duration: 2000,
            });
          })
          .finally(() => {
            this.terminateLoading = false;
            this.terminateDialogVisible = false; // 
          })
    }
  }

};
</script>

<style>
.instance-list {
  display: flex;
  flex-wrap: wrap;
  text-align: right;
}

.instance-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 8px;
  width: 100%;
  max-width: 300px;
  border-radius: 8px;
  background-color: #f9f9f9;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left; /* Adjust text alignment */
}

.header {
  display: flex;
  align-items: center;
}

.server-icon {
  font-size: 24px;
  color: #409eff;
  margin-right: 10px; /* Add margin to separate the icon and text */
}

.ip-item {
  display: flex;
  align-items: center;
  margin: 4px 0;
}

button {
  margin-left: 8px;
  padding: 4px 8px;
  cursor: pointer;
}

.el-divider--horizontal {
  border-top: 1px var(--el-border-color) var(--el-border-style);
  display: block;
  height: 1px;
  margin: 10px 0;
  width: 100%;
}

</style>

